"use client";
import Scene3D from "@/components/Scene3D";
import { ScrollStop } from "@/components/ScrollStop";
import StaggeredMenu from "@/components/StaggeredMenu";
import { useState } from "react";

export default function Home() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleMenuToggle = () => {
    setIsMenuOpen((prev) => !prev);
  };

  return (
    <div className="text-foreground z-10">
      <div>
        <Scene3D />
      </div>
      <main
        className={`main-content h-screen w-full snap-mandatory snap-y overflow-y-auto scroll-smooth pt-20 ${isMenuOpen ? "translate-x-[-260px]" : ""}`}
      >
        <ScrollStop />
      </main>
      <StaggeredMenu
        isFixed={true}
        onMenuOpen={() => setIsMenuOpen(true)}
        onMenuClose={() => setIsMenuOpen(false)}
      />
    </div>
  );
}
